export class Email {
    appName:string;
    firstName:string;
    lastName:string
    emailTo:string;
    emailFrom:string;
    subject:string;
    telephone:string;
    message:string;

    constructor(first:string = null, last:string = null, emailFrom:string = null,
                telephone:string = null, message:string = null) {
        this.appName = "RuthKingTherapist";
        this.firstName = first;
        this.lastName = last;
        this.emailTo = "ruthnking@outlook.com";
        this.emailFrom = emailFrom;
        this.subject = "Client Enquiry";
        this.telephone = telephone;
        this.message = message;
    }
}



