import {Component, ElementRef, ViewChild} from '@angular/core';
import {Email} from "../objects";
import {AppService} from "../app.service";
import {Subscription} from "rxjs/Subscription";

@Component({
    selector: 'rk-contact',
    templateUrl: './contact.component.html'
})
export class ContactComponent {
    public email: Email = new Email();
    public disabled: boolean = false;

    @ViewChild('success') success: ElementRef;
    @ViewChild('error') error: ElementRef;

    constructor(public appService: AppService) { }

    send(form: any, isValid: boolean) {
        let component = this;
        if (!isValid) return false;

        this.disabled = true;
        let email: Email = new Email(this.email.firstName, this.email.lastName,
            this.email.emailFrom, this.email.telephone, this.email.message);

        this.appService.sendEmail(email).subscribe((success) => {
            component.error.nativeElement.classList.remove("show-validation");
            component.success.nativeElement.classList.remove("show-validation");

            if(!success) {
                component.error.nativeElement.classList.add("show-validation");
                return false;
            }

            component.success.nativeElement.classList.add("show-validation");
            component.email = new Email();
            form.resetForm();
            this.disabled = false;
        });
    }
}



